/* ==================
    3. header of mobile
===================== */


.logo {
    animation: logoani 25s infinite;
}

@keyframes logoani {

    0%,
    100% {
        opacity: 0.5;
    }

    50% {
        letter-spacing: 7px;
        opacity: 1;
    }
}


.logo:hover {
    font-size: x-large;
}